import React from "react";
import { Link, useParams } from "react-router-dom";
import ban_data from "../data/ban";

function AlbumDetail() {
  //    id  !
  const { id } = useParams();

  //  id    
  const eachAlbum = ban_data.main[id];

  return (
    <>
      <main id="albumlist_bx" style={{ background: eachAlbum.bgcolor }}>
        <section className="albumlist_wrap">
          {/* 1.   */}
          <div className="contents_left">
            {/* 1-1.   */}
            <div className="back_btn">
              <Link to="/al">
                <span style={{ color: eachAlbum.txcolor.indexOf("^") !== -1 ? eachAlbum.txcolor.split("^")[1] : eachAlbum.txcolor }}>BACK</span>
              </Link>
            </div>
            {/* 1-2.    */}
            <div className="album_cover">
              <img src={"." + eachAlbum.isrc} alt={" " + eachAlbum.mtit + " "} />
            </div>
            {/* 1-3.   */}
            <div className="release_date">
              <p style={{ color: eachAlbum.txcolor.indexOf("^") !== -1 ? eachAlbum.txcolor.split("^")[1] : eachAlbum.txcolor }}>Release Date</p>
              <p style={{ color: eachAlbum.txcolor.indexOf("^") !== -1 ? eachAlbum.txcolor.split("^")[1] : eachAlbum.txcolor }}>{eachAlbum.release}</p>
            </div>
          </div>
          {/* 2.   */}
          <div className="contents_right">
            {/* 2-1.  */}
            <div className="album_tit">
              {/* txcolor  ^   txcolor   */}
              <p style={{ color: eachAlbum.txcolor.indexOf("^") == -1 ? eachAlbum.txcolor : eachAlbum.txcolor.split("^")[0] }}>{eachAlbum.mtit.replace(/-/g, "")}</p>
            </div>
            <div className="album_info">
              {/* txcolor  ^  txcolor.split('^')[1]   */}
              <p style={{ color: eachAlbum.txcolor.indexOf("^") !== -1 ? eachAlbum.txcolor.split("^")[1] : eachAlbum.txcolor }}>
                {eachAlbum.info.split("^").map((segment, index) => (
                  <React.Fragment key={index}>
                    {segment}
                    <br />
                  </React.Fragment>
                ))}
              </p>
            </div>
          </div>
        </section>
        <section className="track-list">
          <h2 className="contents-title" style={{ color: eachAlbum.txcolor.indexOf("^") == -1 ? eachAlbum.txcolor : eachAlbum.txcolor.split("^")[0] }}>
            TRACK
          </h2>
          <div className="list-wrap">
            <ol>
              {eachAlbum.track.map((x, i) => (
                <li key={i}>
                  <span style={{ color: eachAlbum.txcolor.indexOf("^") !== -1 ? eachAlbum.txcolor.split("^")[1] : eachAlbum.txcolor }}>{i + 1 + "." + " " + x}</span>
                </li>
              ))}
            </ol>
          </div>
        </section>
      </main>
    </>
  );
}

export default AlbumDetail;
